<!-- src/components/TodoList.vue -->
<template>
  <div>
    <h1>To-Do List</h1>
    <el-input v-model="newTask" placeholder="What needs to be done?" @keyup.enter="addTask"></el-input>
    <el-button @click="addTask">Add</el-button>
    <p>{{ completedTasksCount }} out of {{ tasks.length }} items completed</p>
    <div v-for="(task, index) in tasks" :key="index">
      <el-checkbox :label="task.name" @change="toggleTaskCompletion(index)">
        {{ task.name }}
      </el-checkbox>
      <el-button @click="editTask(index)">Edit</el-button>
      <el-button type="danger" @click="deleteTask(index)">Delete</el-button>
    </div>
    <el-dialog v-model="dialogVisible" title="Edit Task">
      <el-input v-model="editTaskName" placeholder="Edit task name"></el-input>
      <template v-slot:footer>
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="saveTask">Save</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, computed } from 'vue';

export default {
  setup() {
    const newTask = ref('');
    const tasks = ref([
      { name: 'Learn Vue', completed: false },
      { name: 'Create a Vue project with the CLI', completed: true },
      { name: 'Study ECharts', completed: false },
      { name: 'Use Element Plus', completed: false }
    ]);
    const dialogVisible = ref(false);
    const editTaskName = ref('');
    const editingIndex = ref(null);

    const completedTasksCount = computed(() => tasks.value.filter(task => task.completed).length);

    function addTask() {
      if (newTask.value) {
        tasks.value.push({ name: newTask.value, completed: false });
        newTask.value = '';
      }
    }

    function toggleTaskCompletion(index) {
      tasks.value[index].completed = !tasks.value[index].completed;
    }

    function editTask(index) {
      editingIndex.value = index;
      editTaskName.value = tasks.value[index].name;
      dialogVisible.value = true;
    }

    function saveTask() {
      if (editTaskName.value) {
        tasks.value[editingIndex.value].name = editTaskName.value;
        dialogVisible.value = false;
        editingIndex.value = null;
      }
    }

    function deleteTask(index) {
      tasks.value.splice(index, 1);
    }

    return {
      newTask,
      tasks,
      dialogVisible,
      editTaskName,
      editingIndex,
      completedTasksCount,
      addTask,
      toggleTaskCompletion,
      editTask,
      saveTask,
      deleteTask
    };
  },
};
</script>

<style scoped>
.dialog-footer {
  text-align: right;
}
</style>